:root {
  --primary-color: #2a6259;
  --secondary-color: #e0b354;
  --dark-grey: #2b2b2b;
  --light-grey: #d3d3d3;
  --visited-color: #05676e;
  --active-color: #489c79;
  --background-color: #f0f8ff;
}

a {
  color: var(--primary-color);
}

body {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.6;
  background-color: var(--background-color);
}
